<template>
  <div class="content">
    <div class="top-title">注册复核</div>
    <van-row class="upload_hint text-desc" style="color: #f00; background:#fff; padding:10px;">
      <van-icon name="hint"/>
      审核不通过原因，请按要求修改重新提交审核<br>
      {{user.verify_remarks}}
    </van-row>
    <van-cell-group>
      <van-field :value="user.mobile" label="手机号" required placeholder="请输入手机号"/>
      <van-field v-model="user.name" :value="user.name" label="姓名" required placeholder="请输入姓名"/>
      <van-field v-model="user.taobaoUserId" :value="user.taobaoUserId" label="淘宝号" required placeholder="请输入淘宝号"/>
      <van-field
        label="开户银行"
        required
        v-model="user.bank"
        placeholder="请选择开户银行"
        readonly
        @click="showBank = true"
      />
      <van-field v-model="user.bank_number" :value="user.bank_number" label="卡号" required placeholder="请输入银行卡号"/>
      <van-field v-model="user.id_card" :value="user.id_card" label="身份证号" required placeholder="请输入身份证号"/>

      <mobileCode :mobile="user.mobile" v-bind:scene="`register`"  ref="mobileCode"/>
    </van-cell-group>
    <uploadNew v-if="isUpload" @setFiles = "setFiles" ref="uploadNew"/>
    <van-cell-group style="padding:10px;">
      <van-button :loading="isSubmit" @click="subReviews" type="danger" :bottom-action="true">提交审核</van-button>
    </van-cell-group>
    <van-popup v-model="showBank" position="bottom">
      <van-picker
        show-toolbar
        :columns="bankColumns"
        title="选择银行"
        @cancel="showBank = false"
        @confirm="onBankConfirm"
      />
    </van-popup>
  </div>
</template>
<script>
import {
  Row,
  Col,
  CellGroup,
  Cell,
  Button,
  Dialog,
  ImagePreview,
  Toast,
  Field,
  Picker
} from "vant";

import { REG_REVIEW, USER_PROFILE } from "@/api/user";
import { idCard } from "@/assets/js/regexp";
import uploadNew from "./upload-new";
import mobileCode from "@/common/mobileCode";
import bankList from "../../assets/data/bank";
import validateMobile from "vant/lib/utils/validate/mobile";

export default {
  name: "review",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Button.name]: Button,
    [Toast.name]: Toast,
    [ImagePreview.name]: ImagePreview,
    [uploadNew.name]: uploadNew,
    [mobileCode.name]: mobileCode,
    [Picker.name]: Picker,
  },
  created() {
    this.initPage()
  },
  data() {
    return {
      isUpload:true, //是否需要 上次身份图片
      isSubmit:false,
      showBank: false,
      bankColumns:[],
      user:{
        name: '',
        mobile: '',
        taobaoUserId:'',
        bank: '',
        bank_number: '',
        id_card:'',
        verify_remarks:'',
        v_code: ''
      },
      isShow: true,
      verifyText: '',
      countSeconds: 0,
      files:'',
    };
  },
  methods: {
    initPage(){
      console.dir(USER_PROFILE)
      this.$reqGet(USER_PROFILE)
        .then(res => {
          this.getSuccess(res);
        })
        .catch(err => {
        });
      this.bankColumns = bankList['bank_list']

    },
    getSuccess(res) {
      let data = res.data.data;
      this.user.name = data.name;
      this.user.mobile = data.account;
      this.user.bank = data.bank;
      this.user.taobaoUserId = data.taobaoUser;
      this.user.id_card = data.card_data.card_no;
      this.user.bank_number = data.card_no;
      this.user.verify_remarks = data.verify_remarks;
    },

    setFiles(file){
      this.files = file
    },
    //提交复审
    subReviews(){
      try {
          let _this = this;
          //验证合法性
          let items = ["name","mobile",'taobaoUserId',"bank","bank_number","id_card",]
          items.forEach(function(item,index){
            let msg =_this.validate(item)
            if(msg && msg != ''){
              throw new Error(msg)
            }
          })

          let formdata = new FormData();
          if(this.isUpload){
            console.dir(2222)
            console.dir(this.files)
            if(this.files){
              if(this.files.upFile){
                formdata.append("front", this.files.upFile.file);
              }
              if(this.files.downFile){
                formdata.append("reverse", this.files.downFile.file);
              }
              if(this.files.alipayFile){
                formdata.append("alipayPic", this.files.alipayFile.file);
              }
              if(this.files.bankFile){
                formdata.append("bankPic", this.files.bankFile.file);
              }
              if(this.files.poseFile.file){
                formdata.append("pose", this.files.poseFile.file);
                formdata.append("poseEg", this.files.poseEgName);
              }
            }

          }

          formdata.append("name", this.user.name)
          formdata.append("mobile", this.user.mobile)
          formdata.append("taobaoUserId", this.user.taobaoUserId)
          formdata.append("bank", this.user.bank)
          formdata.append("bank_number", this.user.bank_number)
          formdata.append("card_no", this.user.id_card)
          formdata.append("v_code", this.$refs.mobileCode.v_code)
          this.isSubmit = true
          this.$reqPost(REG_REVIEW, formdata).then(res => {
              this.isSubmit = false
              this.$router.push({ name: "user"});
          }).catch(err => {
              this.isSubmit = false
          });
      } catch (e) {
        Toast(e.message)
      }
    },
    //验证
    validate(key) {
      const value = this.user[key];
      switch (key) {
        case "name":
          return value ? "" : "请填写真实姓名";
        case "mobile":
          if(!value) return "请填写手机号码";
          return validateMobile(value) ? "" : "请输入正确的手机号码";
        case "taobaoUserId":
          return value ? "" : "请输入淘宝账号";
        case "id_card":
           return idCard(value) ? '' : '请输入15位或18位身份证号！';
        case "bank":
          return value ? "" : "请选择银行";
        case "bank_number" :
          return value ? "" : "请填写银行卡号";
        case "v_code" :
          return value ? "" : "请填写验证码";
        default :
          return '';
      }
    },
    //银行选择确认
    onBankConfirm(value, index) {
      this.showBank = false;
      this.user.bank = value.text;
    },
    //返回
    goBack() {
      this.$router.push({name: 'index'});
    },
    countDown() {
      if (this.countSeconds === 0) {
        this.verifyText = "继续";
        return;
      } else {
        this.verifyText = `${this.countSeconds--}秒`;
      }
      setTimeout(() => {
        this.countDown();
      }, 1000);
    },
    continueGo() {
      if(this.countSeconds <=0 ){
        this.isShow = false;
      } else {
        this.isShow = true;
      }
    },
    setVcode(code){
      this.user.v_code = code
    }
  }
};
</script>
<style lang="scss" scoped>
.top-title {
  text-align: center;
  padding: 12px;
  background-color: #fff;
  font-size: 16px;
}
.tip-r {
  color: red;
}
.tip_message{
  font-size: 12px;
}
.mask-layer {
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.75);
  position: fixed;
  top: 0;
  z-index: 999;
  left: 0;
  .content {
    width: 80%;
    min-height: 80%;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    &>div {
      box-sizing: border-box;
      padding: 20px 15px 0 15px;
      p {
        line-height: 1rem;
      }
    }
    .btn {
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}
</style>



