<template>
    <van-field v-model="v_code" required label="手机验证码" placeholder="请输入4位手机验证码">
        <countdown v-if="counting" slot="button" :time="60000" @countdownend="countdownend" class="countdown">
            <template slot-scope="props">{{ +props.seconds || 60 }}秒后获取</template>
        </countdown>
        <van-button v-else slot="button" size="small" type="primary" @click="sendCode">发送验证码</van-button>
    </van-field>
</template>
<script>
import { Row, Col, Toast } from "vant";
import validateMobile from "vant/lib/utils/validate/mobile";
import {USER_SENDCODE } from "@/api/user";
export default {
  name: "mobileCode",
  props: {
      mobile:{
          type: String,
          default: ''
      },
      scene:{
          type: String,
          default: ''
      }
  },
  components: {
      validateMobile,
      Toast,
      USER_SENDCODE
  },
  data() {
    return {
        counting: false,
        v_code: '', //输入的验证码
        phone: this.mobile, //手机号码 父组建传入
        scenes: this.scene, //发送场景 父组建传入
    }
  },
  watch:{
    mobile(newValue){
      this.phone = newValue
    }
  },
  methods: {
    //发送获取验证码
    sendCode() {
        let checkMobile = validateMobile(this.phone)
        if(!checkMobile){
            Toast('请输入正确的手机号码')
            return false
        }
        if (!this.counting) {
            this.$reqPost(USER_SENDCODE, {
                mobile: this.phone,
                operation: this.scenes
            }).then((res) => {
                this.codeBack(res)
            }).catch((err) => {
                this.codeBack(err)
            });
        }
    },
    //验证码回调
    codeBack(res){
        if(res.data.code !=0){
            let msg = res.data.msg ? res.data.msg : "注册提交失败";
            Toast.fail(msg);
            return false
        } else {
            this.counting = true;
            Toast.success('发送成功');
        }
    },
    countdownend() {
        this.counting = false;
    }
  }
}
</script>

<style>
  .countdown {
    color: #4b0;
  }
</style>




