<template>
  <div class="id_card_upload">
      <div class="pay_way_title">身份证上传</div>
      <van-row class="upload_hint text-desc" style="color: darkred">
        <van-icon name="hint"/>
        请按审核原因重新上传认证照片，如没有提示照片不合格，此项可以不必上传！
      </van-row>
      <van-row gutter="20" class="id_card_row upload_box">
      <van-col span=24>
        <div class="upload_wrap">
          <van-uploader capture=“camera” @oversize="maxReadBack" max-size="5242880" :after-read="onRead1">
            <div class="add_btn">
              <div v-if="up_pic">
                  <img :src="up_pic">
                  <div class="transparent"></div>
              </div>
              <div v-else>
                  <van-icon name="photograph" />
                  <p>上传正面</p>
              </div>
            </div>
          </van-uploader>
        </div>
      </van-col>
    </van-row>
    <van-row gutter="10" class="id_card_row">
      <van-col span=24>
        <div class="text-desc red_color">正面示列</div>
        <img :src="demoImg1" width="50%">
      </van-col>
    </van-row>
    <van-row gutter="20" class="id_card_row upload_box">
      <van-col span=24>
        <div class="upload_wrap">
          <van-uploader capture=“camera” @oversize="maxReadBack" max-size="5242880" :after-read="onRead2">
            <div class="add_btn">
              <div v-if="down_pic">
                  <img :src="down_pic">
                  <div class="transparent"></div>
              </div>
              <div v-else>
                  <van-icon name="photograph" />
                  <p>上传反面</p>
              </div>
            </div>
          </van-uploader>
        </div>
      </van-col>
    </van-row>

    <van-row gutter="10" class="id_card_row">
      <van-col span=24>
        <div class="text-desc red_color">反面示列</div>
        <img :src="demoImg2" width="50%">
      </van-col>
    </van-row>
    <div class="pay_way_title">支付宝截图：支付宝-我的--点击头像，截图个人中心</div>
    <van-row gutter="20" class="id_card_row upload_box">
      <van-col span=24>
        <div class="upload_wrap">
          <van-uploader  @oversize="maxReadBack" max-size="5242880" :after-read="onRead4">
            <div class="add_btn">
              <div v-if="alipay_pic">
                <img :src="alipay_pic">
                <div class="transparent"></div>
              </div>
              <div v-else>
                <van-icon name="photograph" />
                <p>上传支付宝截图</p>
              </div>
            </div>
          </van-uploader>
        </div>
      </van-col>
    </van-row>

    <van-row gutter="10" class="id_card_row">
      <van-col span=24>
        <div class="text-desc red_color">支付宝截图示列</div>
        <img :src="demoImg3" width="50%">
      </van-col>
    </van-row>
    <div class="pay_way_title">银行卡截图：请拍摄银行卡正面照片</div>
    <van-row gutter="20" class="id_card_row upload_box">
      <van-col span=24>
        <div class="upload_wrap">
          <van-uploader capture=“camera” @oversize="maxReadBack" max-size="5242880" :after-read="onRead5">
            <div class="add_btn">
              <div v-if="bank_pic">
                <img :src="bank_pic">
                <div class="transparent"></div>
              </div>
              <div v-else>
                <van-icon name="photograph" />
                <p>上传银行卡截图</p>
              </div>
            </div>
          </van-uploader>
        </div>
      </van-col>
    </van-row>
    <div class="pay_way_title">自拍视频：自拍视频验证时准确匀速读出自己姓名，拍摄时必须能看清视频中本人以及本人身份证照片头像！（可以缓慢移动摄像头至身份证处如视频所示）请在光线较好的背景下拍摄。</div>
    <van-row gutter="20" class="id_card_row upload_box">
      <van-col span=24>
        <div class="upload_wrap">
          <van-uploader accept="video/*" capture=“camcorder” @oversize="maxReadBack" max-size="15242880" :after-read="onRead3">
            <div class="add_btn">
              <div v-if="pose_pic">
                <img :src="pose_pic">
                <div class="transparent"></div>
              </div>
              <div v-else>
                <van-icon name="photograph" />
                <p>拍摄视频</p>
              </div>
            </div>
          </van-uploader>
        </div>
      </van-col>
    </van-row>

    <van-row gutter="10" class="id_card_row">
      <van-col span=24>
        <div class="text-desc red_color">自拍视频示列</div>
        <div style="text-align:center; overflow:hidden;">
          <video id="myVideo" class="video-js">
            <source :src="demoImg5" type="video/mp4">
          </video>
        </div>
      </van-col>
    </van-row>

  </div>
</template>
<script>
import { Row, Col, Uploader, ImagePreview,Toast } from "vant";
import pose1 from "@/assets/images/pose1.jpg";
import pose2 from "@/assets/images/pose2.jpg";
import pose3 from "@/assets/images/pose3.jpg";
import pose4 from "@/assets/images/pose4.jpg";
var poseArr = [pose1, pose2, pose3, pose4];

export default {
  name: "uploadNew",
  data() {
    return {
      demoImg1: require(`@/assets/images/front.jpg`),
      demoImg2: require(`@/assets/images/back.jpg`),
      demoImg3:require(`@/assets/images/alipay_01.jpg`),
      demoImg5:require(`@/assets/images/zp_video.mp4`),
      poseEg: pose1,
      up_pic:'',
      down_pic: '',
      pose_pic: '',
      alipay_pic: '',
      bank_pic: '',
      files:{
        upFile:'',
        downFile:'',
        alipayFile:'',
        bankFile:'',
        poseFile:'',// 本人照片
        poseEgName:'' // 本人照片示列
      }
    }
  },
  created() {
    this.poseEg = poseArr[Math.floor(Math.random() * poseArr.length)];
    let poseEgName = "pose1.jpg";
    switch (this.poseEg) {
      case pose1:
        poseEgName = "pose1.jpg";
        break;
      case pose2:
        poseEgName = "pose2.jpg";
        break;
      case pose3:
        poseEgName = "pose3.jpg";
        break;
      case pose4:
        poseEgName = "pose4.jpg";
        break;
    }
    this.files.poseEgName = poseEgName;
  },
  methods: {
    onRead1(files){
      console.log('压缩前的file');
      console.log(files);
      let img = new Image();
      img.src = files.content;
      let that=this;
      img.onload=function(){
        let f1 = that.compress(img);
        that.files.upFile = f1;
        that.up_pic = f1.content
      }
      this.$emit('setFiles',this.files);
    },
    onRead2(files){
      let img = new Image();
      img.src = files.content;
      let that=this;
      img.onload=function(){
        let f1 = that.compress(img);
        that.files.downFile = f1;
        that.down_pic = f1.content
      }
      this.$emit('setFiles',this.files);
    },
    onRead3(files){
      let img = new Image();
      img.src = files.content;
      let that=this;
      img.onload=function(){
        let f1 = that.compress(img);
        that.pose_pic = f1.content
      }
      that.files.poseFile = files;
      this.$emit('setFiles',this.files);
    },
    onRead4(files){
      let img = new Image();
      img.src = files.content;
      let that=this;
      img.onload=function(){
        let f1 = that.compress(img);
        that.files.alipayFile = f1;
        that.alipay_pic = f1.content
      }
      this.$emit('setFiles',this.files);
    },
    onRead5(files){
      let img = new Image();
      img.src = files.content;
      let that=this;
      img.onload=function(){
        let f1 = that.compress(img);
        that.files.bankFile = f1;
        that.bank_pic = f1.content
      }
      this.$emit('setFiles',this.files);
    },
    // 压缩图片
    compress(img){
        let originWidth = img.width, // 压缩后的宽
        originHeight=img.height,
        maxWidth = 600, maxHeight = 400,
        quality = 0.8, // 压缩质量
        canvas = document.createElement('canvas'),
        drawer = canvas.getContext("2d");
        canvas.width = maxWidth;
        canvas.height = originHeight/originWidth*maxWidth;
        drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
        let base64 = canvas.toDataURL("image/jpeg", quality); // 压缩后的base64图片
        let file = this.dataURLtoFile(base64,Date.parse(Date())+'.jpg');
        file={content:base64,file:file};
        //console.log('压缩后的file');
        //console.log(file);//压缩后的file
        return file;
    },
     // base64转file
    dataURLtoFile(dataurl,filename){
      let arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr],filename,{type:mime});
    },
    // 图片超出限制后 进行压缩
    maxReadBack(files){
      Toast('图片不能超出5M')
    }
  },
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [ImagePreview.name]: ImagePreview,
    [Uploader.name]: Uploader,
    [Toast.name]: Toast
  }
}
</script>
<style lang="scss" scoped>
@import "../../assets/scss/trans";
.pay_way_title {
  font-weight: bold;
  padding: 0;
}
.id_card_upload {
  margin: 5px 0 5px 0;
  background-color: #fff;
  padding: 15px;
  > div {
    margin-bottom: 15px;
  }
}
.upload_hint {
  position: relative;
  padding-left: 20px;
  line-height: 1.6;
  i {
    position: absolute;
    top: 2px;
    left: 0;
  }
}
.id_card_row {
  > div {
    text-align: center;
  }
  .text-desc {
    margin-bottom: 8px;
  }
  .red_color {
    color: darkred;
    font-size: 14px;
    font-weight: 700;
  }
}
.upload_box .upload_wrap {
  position: relative;
  border: 1px dashed $gray;
  min-height: 145px;
  box-sizing: border-box;
  padding: 5px;
  text-align: center;
  img {
    max-width: 100%;
    height: 120px;
  }
  .van-uploader {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  .add_btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: $gray;
    p {
      white-space: nowrap;
      margin: 0;
      font-size: 18px;
    }
    i {
      font-size: 26px;
    }
  }
}
</style>





